import React from "react";
import Layout from "../components/layout";
import Tabs from "../components/tabs";
import Member from "../components/member";
import Button from "../components/button";

const AddMember = props => (
  <div className="level" style={{marginBottom: `10px`}}>
    <div className="level-litem">
      <Button onClick= {props.onClick} class="fas fa-lg fa-user-alt has-text-info" text="Kara Mamobbi" />
    </div>
  </div>
)

class MembersList extends React.Component {

  constructor(props) {
    super(props);
    this.addMember = this.addMember.bind(this);
    this.state = {
      members: props.members
    };
    this.numbers = [
      "754238536", "05489806", "654593536", "15463536", "95464536"
    ]
    this.names = ["Assibi", "Cof", "Coffi", "Coffie", "Colee", "Coley", "Colie", "Dura", "Durah", "Durrah", "Fara", "Fari", "Farih", "Farra", "Farry", "Gaddo", "Gambo", "Jummai", "Ladi"];
  }
  addMember() {
    console.log("button pressed");
    console.log(this.state);
    this.setState({
      members: [
        {name: this.names[Math.floor(Math.random() * this.names.length) + 1], phone: this.numbers[Math.floor(Math.random() * this.numbers.length) + 1], shares: Math.floor(Math.random() * 50) + 1, loans: Math.floor(Math.random() * 20) + 1},
        ...this.state.members
      ]
    })
  }

  render() {
    return (
      <div>
        <AddMember onClick={this.addMember}/>
        {this.state.members.map((member, index) => (
          <Member key={index} name={member.name} phone={member.phone} shares={member.shares} loans={member.loans} />
        ))}
      </div>
   )
  }
}

export default ({ pageContext: { allData } }) => (
  <Layout>
    <Tabs />
    <MembersList members={allData.members}/>
  </Layout>
);
